// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";
html, body {
  height: 100%; 
}

.navbar-inverse {
	background:#16a085;
	border-color:#16a085;
}

.navbar {
    border-radius: 0px;
    border:0px;
}
.navbar-nav{
	margin:10px 0 10px 100px;
}
.navbar-inverse .navbar-nav>li>a {
    color: white;
    font-size: 1.2em;
}

.navbar-inverse .navbar-nav>li>a:hover {
    color: #ecf0f1;
}

.navbar-inverse .navbar-brand {
    color: white;
}

.navbar-inverse .navbar-brand:hover {
    color: #ecf0f1;
}

#rechercher {
	background:#e67e22;
}

#rechercher:hover {
	box-shadow: -2px 2px 10px 0px #2c3e50;
}

#proposer {
	background:#3498db;
}

#proposer:hover {
	box-shadow: -2px 2px 10px 0px #2c3e50;
}

#marche {
	background:#f1c40f;
}

#marche:hover {
	box-shadow: -2px 2px 10px 0px #2c3e50;
}

.navbar-inverse .navbar-nav>.open>a, .navbar-inverse .navbar-nav>.open>a:focus, .navbar-inverse .navbar-nav>.open>a:hover {
	background:#16a085;
}

#preferences {
	background-color:rgba(46, 204, 113,0.3);
}

#preferences p {
	color:white;
	font-size: 1.3em;
	background:rgba(26, 188, 156,1.0);
	text-align:center;
}

#site {
	width:100%;
}

#dejamembre {
	float:right;
}

#dejamembre:hover {
	color:#3498db;
}

#datetimepicker1 {
	width:100%;

}
